export default ".navbar {\n  box-shadow: 2px 2px 10px black;\n  background: #da22ff; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */\n  background: linear-gradient(\n    to right,\n    #9733ee,\n    #da22ff\n  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */\n}\n.brand-title {\n  font-family: \"Josefin Sans\", sans-serif;\n  color: aliceblue;\n  font-size: 20px;\n}\n.nav-item {\n  transition: all 0.2s ease-in;\n  transform: scale(1);\n}\n.active {\n  color: aliceblue;\n}\n/* Only desktop */\n@media screen and (min-device-width: 1200px) and (max-device-width: 1600px) {\n  .nav-item {\n    font-size: 15px;\n    /* color: white; */\n\n    line-height: 50px;\n    position: relative;\n    z-index: 1;\n    display: inline-block;\n    text-align: center;\n  }\n  .active {\n    color: aliceblue;\n    transform: scale(1.2);\n  }\n  .nav-item:hover {\n    transform: scale(1.2);\n    font-weight: bold;\n  }\n  .animation {\n    position: absolute;\n    height: 100%;\n    top: 0;\n    z-index: 0;\n    background: #c2185b;\n    border-radius: 8px;\n    transition: all 0.5s ease;\n  }\n\n  ul li:nth-child(1) {\n    /* width: 100px; */\n    /* margin-right: 1.5em;\n    margin-left: 1em; */\n    width: 100px;\n  }\n\n  .start-home,\n  ul li:nth-child(1):hover ~ .animation {\n    width: 110px;\n    left: 500px;\n  }\n  ul li:nth-child(2) {\n    width: 100px;\n    /* margin-right: 1.5em; */\n  }\n  .start-world,\n  ul li:nth-child(2):hover ~ .animation {\n    width: 110px;\n    left: 600px;\n  }\n  ul li:nth-child(3) {\n    width: 100px;\n  }\n  .start-news,\n  ul li:nth-child(3):hover ~ .animation {\n    width: 110px;\n    left: 700px;\n  }\n  ul li:nth-child(4) {\n    width: 150px;\n    /* margin-left: 5em; */\n  }\n  .start-mythb,\n  ul li:nth-child(4):hover ~ .animation {\n    width: 160px;\n    left: 800px;\n  }\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"